* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  font-size: 14px;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), url("../fonts/Roboto-Regular.ttf") format("ttf");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: local("Roboto"), url("../fonts/Roboto-Medium.ttf") format("ttf");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto"), url("../fonts/Roboto-Bold.ttf") format("ttf");
}

@keyframes topToCenter {
  0% {
    top: -100%;
  }
  100% {
    top: 80px;
  }
}

@keyframes centrToBottom {
  0% {
    top: 80px;
  }
  100% {
    top: 100%;
  }
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 96vw;
  padding: 0 2vw;
  margin: 20px auto;
  background-color: #f0f8ff;
  border-radius: 20px;
  box-shadow: 0 5px 4px 2px rgba(34, 60, 80, 0.2);
}

.header__logo {
  display: inline-block;
  width: 48px;
  height: 48px;
  margin-right: 25px;
  background-image: url("../images/vulcansalutel.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: 2px solid #b0b0b0;
  border-radius: 10px;
}

.header__contacts {
  display: grid;
  grid-template-rows: repeat(2, 32px);
  grid-template-columns: repeat(2, max-content);
  grid-gap: 5px;
  align-items: center;
}

.header__title {
  display: flex;
  flex-grow: 1;
  font-size: 28px;
  line-height: 32px;
}

@media screen and (max-width: 600px) {
  .header__wrapper {
    display: grid;
    grid-template-rows: 60px max-content;
    grid-template-columns: 60px 1fr;
  }
  .header__logo {
    grid-area: 1/1/2/2;
  }
  .header__contacts {
    grid-area: 2/1/3/3;
  }
}

.burger {
  position: fixed;
  top: 10px;
  right: 3vw;
  z-index: 100;
  display: none;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  cursor: pointer;
  background-color: #a2caec;
  border: 1px solid #b0b0b0;
  border-radius: 50%;
  box-shadow: 4px 4px 8px 0 rgba(34, 60, 80, 0.2);
}

.burger__icon {
  position: relative;
  width: 40px;
  height: 5px;
  background-color: #fff;
}

.burger__icon::before {
  position: absolute;
  top: -13px;
  left: 0;
  width: 40px;
  height: 5px;
  content: '';
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon::after {
  position: absolute;
  top: 13px;
  left: 0;
  width: 40px;
  height: 5px;
  content: '';
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon.burger__icon_active {
  background-color: transparent;
}

.burger__icon.burger__icon_active::before {
  top: 0;
  background-color: #fff;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  transform: rotate(45deg);
}

.burger__icon.burger__icon_active::after {
  top: 0;
  background-color: #fff;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  transform: rotate(-45deg);
}

@media screen and (max-width: 460px) {
  .burger {
    display: flex;
  }
}

.burger_disable {
  display: none;
}

.navigation {
  position: relative;
  display: flex;
  grid-area: 1/1/2/2;
  width: 100%;
  min-width: 100px;
  height: 100%;
  padding: 10px;
  background-color: #f0f8ff;
  border-radius: 20px;
}

.navigation__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}

.navigation__items {
  position: sticky;
  top: 10px;
}

.navigation__item:not(:last-child) {
  margin-bottom: 25px;
}

.navigation__item:hover {
  background-color: #a2caec;
  border-radius: 20px;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.navigation__link {
  display: inline-block;
  width: 100%;
  padding-top: 80px;
  padding-bottom: 10px;
  font-size: 18px;
  line-height: 22px;
  color: #1d1c1c;
  text-align: center;
  background-repeat: no-repeat;
  background-position: center 10px;
  background-size: auto 60px;
}

.navigation__item:nth-child(1) .navigation__link {
  background-image: url("../images/navigation1.webp");
}

.navigation__item:nth-child(2) .navigation__link {
  background-image: url("../images/navigation2.webp");
}

.navigation__item:nth-child(3) .navigation__link {
  background-image: url("../images/navigation3.webp");
}

@media screen and (max-width: 950px) {
  .navigation {
    position: sticky;
    top: 0;
    z-index: 10;
    height: max-content;
    border: 1px solid #b0b0b0;
  }
  .navigation__wrapper {
    align-items: center;
    width: 100%;
    height: 100%;
  }
  .navigation__items {
    position: relative;
    top: 0;
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
  }
  .navigation__item {
    height: max-content;
    padding: 0 15px;
  }
  .navigation__item:not(:last-child) {
    margin-bottom: 0;
  }
  .navigation__link {
    padding-top: 55px;
    padding-bottom: 5px;
    background-position: center 5px;
    background-size: auto 40px;
  }
}

@media screen and (max-width: 460px) {
  .navigation {
    position: fixed;
    top: 80px;
    z-index: 10;
    width: 96vw;
  }
  .navigation__items {
    position: relative;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
  }
  .navigation__item:not(:last-child) {
    margin-bottom: 25px;
  }
}

.toCenter {
  animation: topToCenter 0.3s forwards;
}

.toBottom {
  animation: centrToBottom 0.3s forwards;
}

.contents {
  display: flex;
  grid-area: 1/2/2/3;
  width: 100%;
  background-color: #f0f8ff;
  border-radius: 20px;
}

.contents__wrapper {
  padding: 20px;
}

.contents__item {
  display: flex;
  flex-direction: column;
  padding-top: 15px;
  padding-bottom: 45px;
}

.contents__item:last-child {
  min-height: 100vh;
}

.contents__item:not(:last-child) {
  border-bottom: 2px solid #b0b0b0;
}

.contents__works-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 25px;
  padding-bottom: 45px;
}

.contents__works-item {
  width: 49%;
  height: auto;
  margin-top: 1vw;
  border: 1px solid gray;
  transition: 0.3s;
}

.contents__works-item:hover {
  z-index: 2;
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  transition: 0.3s;
  transform: scale(1.07);
}

.contents__works-item:active {
  box-shadow: 0 5px 10px 9px rgba(34, 60, 80, 0.44) inset;
  transform: scale(1);
}

.contents__img {
  width: 100%;
  height: auto;
}

.contents__title {
  font-size: 22px;
  line-height: 32px;
}

.contents__text {
  padding-top: 10px;
  font-size: 18px;
  line-height: 28px;
  color: #4b5357;
}

.contents__list-skills {
  display: flex;
  flex-wrap: wrap;
}

.contents__item-skills {
  padding: 5px;
  font-size: 20px;
  line-height: 25px;
}

.contents__links {
  font-size: 18px;
  line-height: 28px;
}

@media screen and (max-width: 950px) {
  .contents {
    grid-area: 2/1/3/2;
  }
  .contents__item:last-child {
    min-height: initial;
  }
}

@media screen and (max-width: 460px) {
  .contents__works-item {
    width: 98%;
  }
}

body {
  font-family: "Roboto", sans-serif;
  background: radial-gradient(circle, rgba(39, 89, 175, 0.4) 0, rgba(72, 196, 236, 0.4) 100%);
}

.container {
  display: grid;
  grid-template-rows: max-content;
  grid-template-columns: 12% 85%;
  gap: 3%;
  width: 96vw;
  margin: 0 auto;
}

.stopScroll {
  overflow: hidden;
}

.hidde {
  display: none;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  margin-top: 15px;
  background-color: #f0f8ff;
  border-radius: 20px 20px 0 0;
}

.footer__smoll {
  color: #4b5357;
}

@media screen and (max-width: 950px) {
  .container {
    position: relative;
    grid-template-rows: repeat(2, max-content);
    grid-template-columns: 100%;
    gap: 25px;
  }
}
