.contents {
  display: flex;
  grid-area: 1/2/2/3;
  width: 100%;
  background-color: $bgItem;
  border-radius: 20px;

  &__wrapper {
    padding: 20px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    padding-top: 15px;
    padding-bottom: 45px;
  }

  &__item:last-child {
    min-height: 100vh;
  }

  &__item:not(:last-child) {
    border-bottom: 2px solid $border;
  }

  &__works-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 25px;
    padding-bottom: 45px;
  }

  &__works-item {
    width: 49%;
    height: auto;
    margin-top: 1vw;
    border: 1px solid gray;
    transition: 0.3s;
  }

  &__works-item:hover {
    z-index: 2;
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
    transition: 0.3s;
    transform: scale(1.07);
  }

  &__works-item:active {
    box-shadow: 0 5px 10px 9px rgba(34, 60, 80, 0.44) inset;
    transform: scale(1);
  }

  &__img {
    width: 100%;
    height: auto;
  }

  &__title {
    font-size: 22px;
    line-height: 32px;
  }

  &__text {
    padding-top: 10px;
    font-size: 18px;
    line-height: 28px;
    color: $gray;
  }

  &__list-skills {
    display: flex;
    flex-wrap: wrap;
  }

  &__item-skills {
    padding: 5px;
    font-size: 20px;
    line-height: 25px;
  }

  &__links {
    font-size: 18px;
    line-height: 28px;
  }
}

@media screen and (max-width: 950px) {
  .contents {
    grid-area: 2/1/3/2;

    &__item:last-child {
      min-height: initial;
    }
  }
}

@media screen and (max-width: 460px) {
  .contents {
    &__works-item {
      width: 98%;
    }
  }
}
