.burger {
  position: fixed;
  top: 10px;
  right: 3vw;
  z-index: 100;
  display: none;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  cursor: pointer;
  background-color: $hover-element;
  border: 1px solid $border;
  border-radius: 50%;
  box-shadow: 4px 4px 8px 0 rgba(34, 60, 80, 0.2);
}

.burger__icon {
  position: relative;
  width: 40px;
  height: 5px;
  background-color: #fff;
}

.burger__icon::before {
  position: absolute;
  top: -13px;
  left: 0;
  width: 40px;
  height: 5px;
  content: '';
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon::after {
  position: absolute;
  top: 13px;
  left: 0;
  width: 40px;
  height: 5px;
  content: '';
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon.burger__icon_active {
  background-color: transparent;
}

.burger__icon.burger__icon_active::before {
  top: 0;
  background-color: #fff;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  transform: rotate(45deg);
}

.burger__icon.burger__icon_active::after {
  top: 0;
  background-color: #fff;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  transform: rotate(-45deg);
}

@media screen and (max-width: 460px) {
  .burger {
    display: flex;
  }
}

.burger_disable {
  display: none;
}
