.navigation {
  position: relative;
  display: flex;
  grid-area: 1/1/2/2;
  width: 100%;
  min-width: 100px;
  height: 100%;
  padding: 10px;
  background-color: $bgItem;
  border-radius: 20px;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
  }

  &__items {
    position: sticky;
    top: 10px;
  }

  &__item:not(:last-child) {
    margin-bottom: 25px;
  }

  &__item:hover {
    background-color: $hover-element;
    border-radius: 20px;
    box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  }

  &__link {
    display: inline-block;
    width: 100%;
    padding-top: 80px;
    padding-bottom: 10px;
    font-size: 18px;
    line-height: 22px;
    color: $black;
    text-align: center;
    background-repeat: no-repeat;
    background-position: center 10px;
    background-size: auto 60px;
  }

  &__item:nth-child(1) &__link {
    background-image: url("../images/navigation1.webp");
  }

  &__item:nth-child(2) &__link {
    background-image: url("../images/navigation2.webp");
  }

  &__item:nth-child(3) &__link {
    background-image: url("../images/navigation3.webp");
  }
}

@media screen and (max-width: 950px) {
  .navigation {
    position: sticky;
    top: 0;
    z-index: 10;
    height: max-content;
    border: 1px solid $border;

    &__wrapper {
      align-items: center;
      width: 100%;
      height: 100%;
    }

    &__items {
      position: relative;
      top: 0;
      display: flex;
      justify-content: space-evenly;
      width: 100%;
      height: 100%;
    }

    &__item {
      height: max-content;
      padding: 0 15px;
    }

    &__item:not(:last-child) {
      margin-bottom: 0;
    }

    &__link {
      padding-top: 55px;
      padding-bottom: 5px;
      background-position: center 5px;
      background-size: auto 40px;
    }
  }
}

@media screen and (max-width: 460px) {
  .navigation {
    position: fixed;
    top: 80px;
    z-index: 10;
    width: 96vw;

    &__items {
      position: relative;
      top: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      width: 100%;
      height: 100%;
    }

    &__item:not(:last-child) {
      margin-bottom: 25px;
    }
  }
}

.toCenter {
  animation: topToCenter 0.3s forwards;
}

.toBottom {
  animation: centrToBottom 0.3s forwards;
}
